import { Component, h } from '@stencil/core';
/**
 * Height of the editor
 *
 * The text editor automatically adjusts its own height to fit the content inside.
 * So as the user types, the editor will grow taller, potentially resizing its own
 * container element.
 *
 * However, you can constrain the text editor to never grow beyond a certain height,
 * using `--text-editor-max-height` property.
 *
 * In this example, the maximum height is set to `15rem`, which means that:
 * 1. the editor will adjust itself to the content inside,
 * pushing out its container and making it taller, until it reaches `15rem`.
 * 1. and also the editor will try to fill the available surface area
 * when you manually resize the container,
 * (or some other element causes the container to grow taller), until it reaches `15rem`.
 *
 * :::important
 * Naturally, if the container has a fixed small height,
 * The editor which has a lot of content will grow taller than the container.
 * Depending on the `overflow` property of the container,
 * you may get undesired rendering, or parts of the content
 * can become invisible for the end-users.
 *
 * You can test this by resizing this example, and then typing
 * several lines of text!
 *
 * Make sure that `--text-editor-max-height` is set to a value that
 * correlates with the hardcoded height of the container.
 * :::
 */
@Component({
    tag: 'limel-example-text-editor-height',
    shadow: true,
    styleUrl: 'text-editor-height.scss',
})
export class TextEditorHeightExample {
    public render() {
        return <limel-text-editor />;
    }
}
